import { Component, OnInit } from "@angular/core";
import { DatePipe } from "@angular/common";
import { HttpErrorResponse } from "@angular/common/http";
import * as $ from "jquery";
import { CreateQuoteService } from 'src/app/core/services/create-quote-service';
import { CommonService } from 'src/app/core/services/common-service';
import { DiscussionForumService } from 'src/app/core/services/discussion-forum.service';
import { Section } from 'src/app/Models/section.model';
@Component({
  // selector: 'app-create-quote',
  templateUrl: "./create-section.component.html",
  styleUrls: ["./create-section.component.css"],
})
export class CreateSectionComponent implements OnInit {
  sucesserror: string="";
  sectionObj = new Section();
  response: any;
  constructor(
  public datepipe: DatePipe,
  private createQuoteService: CreateQuoteService, 
  private commonService: CommonService,
  private discussionfourmService: DiscussionForumService)
  {
    
  }

  ngOnInit() 
  {
    this.sucesserror="";
  }

  saveSectionDetails()
  {
    if(this.sectionObj.name == "" || this.sectionObj.name == null)
    {
      this.sucesserror="Please enter section";
      $("#AlertModal").show();
      return;
    }
    if(this.sectionObj.description == "" || this.sectionObj.description == null)
    {
      this.sucesserror="Please enter content";
      $("#AlertModal").show();
      return;
    }
    this.discussionfourmService.createSectionDetails(this.sectionObj).subscribe((res) => {
    this.response = res;
    this.sectionObj.name="";
    this.sectionObj.description="";
    this.sucesserror="Section Created Successfully";
    $("#AlertModal").show();
    });
  }
}